<template>
  <div class="list-page">
    <VerticalTabs @on-click="changeSwaggerTab">
      <VerticalTabPanel v-for="(item, index) in swaggerConfig" :label="item.name" :key="index">
        <CodePanel :item="item" />
        <Spin size="large" v-if="item.loading"></Spin>
      </VerticalTabPanel>
    </VerticalTabs>
  </div>
</template>

<script>
import http from '@/utils/http'
import _ from 'loadsh'
import CodePanel from '@/components/Code/index'
import VerticalTabs from '@/components/VerticalTabs/VerticalTabs'
import VerticalTabPanel from '@/components/VerticalTabs/VerticalTabPanel'
export default {
  name: '',
  components: {
    CodePanel,
    VerticalTabs,
    VerticalTabPanel
  },
  data () {
    return {
      swaggerApiList: [],
      swaggerApiGroupList: {},
      swaggerConfigDetail: null,
      swaggerConfig: [],
      activeIndex: 0
    }
  },
  mounted () {
    this.swaggerConfig = _.cloneDeep(this.$store.state.app.swaggerConfig)
    this.getSwaggerApiData(this.swaggerConfig[0])
  },
  methods: {
    getSwaggerApiData (swagger) {
      this.$set(swagger, 'loading', true)
      http.get(swagger.apiUrl).then(res => {
        this.swaggerConfigDetail = res
        if (res.paths) {
          const apiList = []
          Object.keys(res.paths).forEach(key => {
            const path = res.paths[key]
            let mutipleRequest = false // Url
            if (Object.keys(path).length > 1) {
              mutipleRequest = true
            }
            ['get', 'post', 'put', 'delete'].forEach(type => {
              const curItem = path[type]
              if (curItem) {
                let requestCode = ''
                // urlpathParam
                let funcName = key
                  .split('/')
                  .filter(x => !x.startsWith('{'))
                  .pop()
                // 
                if (mutipleRequest) {
                  funcName = `${funcName}_${type}`
                }
                curItem.parameters = curItem.parameters || []
                const requestParams = []
                const pathParams = curItem.parameters
                  .filter(x => x.in === 'path')
                  .map(x => x.name)
                const haveQueryParams =
                  curItem.parameters.findIndex(x => x.in === 'query') >= 0
                const haveBodyParams =
                  curItem.parameters.findIndex(x => x.in === 'body') >= 0
                if (pathParams.length > 0) {
                  requestParams.push(...pathParams)
                }
                if (haveQueryParams) {
                  requestParams.push('params')
                }
                if (haveBodyParams) {
                  requestParams.push('data')
                }
                // key
                let requestUrl = `'${key}'`
                // xxx/{sysno}xxx/${sysno}
                if (pathParams.length > 0) {
                  requestUrl = `\`${key.replace(/\/{/g, '/${')}\``
                }
                debugger
                requestCode = `
                      ${curItem.summary ? '//' + curItem.summary : ''} 
                      export const ${this.convertFuncName(funcName)}= (${requestParams.length > 0 ? requestParams.join(',') : ''
                  })=> { 
                          return request({ 
                            url: ${requestUrl}, 
                            method: '${type}',
                            ${haveQueryParams ? 'params: params' : ''}
                            ${haveBodyParams ? 'data: data' : ''}
                            actionKey:'${this.getControllerName(path[type].tags[0], res)}${this.convertFuncName(funcName)}'
                          }) 
                      }`
                  .split('\n')
                  .filter(x => x.trim().length > 0)
                  .join('\n')
                // 
                requestCode = requestCode
                  .split('\n')
                  .map(item => {
                    return item.replace('                     ', '')
                  })
                  .join('\n')
                apiList.push({
                  tag: this.getTagName(path[type].tags[0], res),
                  url: key,
                  type: type,
                  summary: path[type].summary,
                  requestCode: requestCode
                })
              }
            })
          })
          this.$set(swagger, 'swaggerApiGroupList', _.groupBy(apiList, 'tag'))
          this.$set(swagger, 'swaggerApiList', apiList)
        }
      }).finally(() => {
        swagger.loading = false
      })
    },
    getTagName (tag, swagger) {
      const curTag = swagger.tags.find(x => x.name === tag)
      if (curTag) {
        return `${curTag.name} ${curTag.description}`
      } else {
        return tag
      }
    },
    getControllerName (tag, swagger) {
      const curTag = swagger.tags.find(x => x.name === tag)
      return curTag.name.replace('controller', '').replace(' ', '-').trim()
    },
    changeSwaggerTab (index) {
      const curSwagger = this.swaggerConfig[index]
      if (!curSwagger.swaggerApiGroupList) {
        this.getSwaggerApiData(curSwagger)
      }
    },
    convertFuncName (name) {
      if (name.includes('-')) {
        const strs = name.split('-')
        return strs[0] + strs.slice(1, 100).map(item => this.upperFirstConcat(item)).join('')
      }
      return name
    },
    upperFirstConcat (str) {
      return str.substring(0, 1).toLocaleLowerCase() + str.substring(1)
    }
  }
}
</script>

